<template>
  <div v-if="radios"  :class="['radioplay',{pause:pauseRadio}]" >
       <div class="back">
        <router-link to="/recommend"> <img src="../assets/img/back.png"></router-link>
       </div>
    <div class="top">
        <h2>{{radio.name}}</h2>
    </div>

    <div class="content">
      <div class="left">
          <img  :src="`${radio.picUrl}`" >
      </div>
      <div class="right">
          <p> {{radio.desc}}</p>
      </div>
<audio  autoplay 
         controls 
         @play="pauseRadio = false"
         @pause="pauseRadio=true"
         :src="radios.data[0].url"></audio>
    </div>
  </div>
</template>

<script>
export default {
  props: ["radId"],
  data() {
    return {
      radio: "",//内容
      radios:"",//音频
      pauseRadio:false,
    };
  },
 
  created() {
    this.$axios.get("/dj/detail?rid=" + this.radId).then((data) => {
      this.radio = data.data.djRadio;
      // console.log(data)
    });
    this.$axios.get("/song/url?id=478446370").then((d) => {
        this.radios = d.data
      // console.log(d.data)
    });
  },
};
</script>

<style lang="less" scoped>
audio{
    width: 70vw;
    margin: 0px auto;
    height: 100px;
}
.radioplay{
    height: 100vh;
    background-color:rgb(241, 243, 244);
    padding: 10px;
    position: relative;
    .back{
      position: absolute;
      top: 10px;
      left: 10px;
    }
    .top{
        text-align: center;
        margin: 10px;
        font-size: larger;
    }
    .content{
        height: 90vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        .left{
            margin: 10px auto;
            img{
        width: 40vh;
        border-radius: 50%;
        animation: rotate 4s 0s infinite linear;      
        }
        }
        .right{
            text-indent: 2em;
            padding: 10px;
            line-height: 1.5;
        }
    }
}

.pause{
    .content{
        .left{
            img{
     animation-play-state: paused ;

            }
        }
    }

}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>